import { Component, h, State } from '@stencil/core';
/**
 * Trigger shortcuts
 * Typing certain characters will act as shortcuts for triggering a
 * certain functionality.
 */
@Component({
    tag: 'limel-example-text-editor-trigger-shortcuts',
    shadow: true,
})
export class TextEditorTriggerShortcutsExample {
    @State()
    private value: string;

    public render() {
        return [
            <limel-text-editor
                value={this.value}
                onChange={this.handleChange}
            />,
            <limel-example-value value={this.value} />,
        ];
    }

    private handleChange = (event: CustomEvent<string>) => {
        this.value = event.detail;
    };
}
